.card {
    margin: 1em;
}
.center-screen-spinner {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    padding-top: 1em;
}

.text-right{
    text-align: right;
}
@media (pointer: coarse), (hover: none) {
    [title] {
      position: relative;
      display: inline-flex;
      justify-content: center;
    }
    [title]:focus::after {
      content: attr(title);
      position: absolute;
      top: 90%;
      color: #000;
      background-color: #fff;
      border: 1px solid;
      width: fit-content;
      padding: 3px;
    }
}